import AbstractComponent from "../abstract-component";
import './posts.css';

const createPostItem = (data) => {
    const {title, body} = data;
    return (
        `<li class="posts-list__item">
            <h3>${title}</h3>
            <p>${body}</p>
        </li>`
    )
};

const createTemplate = (data) => {
    const postsList = data
        .map((dataItem) => createPostItem(dataItem))
        .join('');

    return (
        `<ul class="posts-list">
            ${postsList}
        </ul>`
    );
};

export default class Posts extends AbstractComponent {
    constructor(data) {
        super();
        this._data = data.value;
    }

    getTemplate() {
        return createTemplate(this._data);
    }
}
