import {Method, STATUS_OK, STATUS_REDIRECT} from './utils/constants';

const checkStatus = (response) => {
  if (response.status >= STATUS_OK && response.status < STATUS_REDIRECT) {
    return response;
  } else {
    throw new Error(`${response.status}: ${response.statusText}`);
  }
};

export default class Api {
  getUserData() {
    return this._load({url: `https://randomuser.me/api/`})
      .then((response) => response.json());
  }

  getFriendsList() {
    return this._load({url: `https://reqres.in/api/users?page=1`})
      .then((response) => response.json())
  }

  getPosts() {
    return this._load({url: `https://jsonplaceholder.typicode.com/posts`})
      .then((response) => response.json())
  }

  _load({url, method = Method.GET, body = null, headers = new Headers()}) {
    return fetch(`${url}`, {method, body, headers})
      .then(checkStatus)
      .catch((err) => {
        throw err;
      });
  }
}
