export default class FriendsModel {
    constructor() {
        this._friends = [];
    }

    getFriends() {
        return this._friends;
    }

    setFriends(friends) {
        this._friends = Array.from(friends);
    }

    filterFriendsByQuery(query) {
        if (query) {
            return this.getFriends().slice()
                .filter((friend) => {
                    const {email, first_name, last_name} = friend;
                    return [email, first_name, last_name].some((data) => {
                        return data.indexOf(query) !== -1;
                    })
                });
        }

        return this.getFriends();
    }
}
