import {FRIENDS_COUNT, RenderPosition} from "./constants";

const removeClass = (container, className) => {
    const element = container.querySelector(`.${className}`);
    element.classList.remove(className);
};

const trottling = (func, ms) => {
    let lastCall = 0;

    return function (...args) {
        const now = (new Date).getTime();

        if (now - lastCall < ms) {
            return;
        }

        lastCall = now;

        return func(...args);
    }
};

const getPageCount = (list) => {
    return Math.round(list.length / FRIENDS_COUNT);
};

const sliceList = (list, startPoint, stopPoint) => {
    return list.slice(startPoint, stopPoint);
};

const createElement = (template) => {
    const newElement = document.createElement(`div`);
    newElement.innerHTML = template;

    return newElement.firstElementChild;
};

const render = (container, template, place) => {
    switch (place) {
        case RenderPosition.AFTER_BEGIN:
            container.prepend(template);
            break;
        case RenderPosition.BEFORE_END:
            container.append(template);
            break;
    }
};

const remove = (component) => {
    component.getElement().remove();
    component.removeElement();
};

export {removeClass, trottling, getPageCount, sliceList, createElement, render, remove};
