import AbstractComponent from "../abstract-component";
import './search.css';

const createTemplate = () => {
    return (
        `<form class="search-form">
            <label for="search-input" class="visually-hidden">Поиск...</label>
            <input type="text" id="search-input" name="search-input" placeholder="filter friends">
        </form>`
    );
};

export default class Search extends AbstractComponent {
    getTemplate() {
        return createTemplate();
    }

    setSearchStringChangeHandler(handler) {
        this.getElement()
            .querySelector('#search-input')
            .addEventListener('input', handler);
    }

    setFormSubmitHandler(handler) {
        this.getElement().addEventListener('submit', handler);
    }
}
