import AbstractComponent from "../abstract-component";
import './about-user.css';

const createTemplate = (data) => {
    const {
        name: {
            first,
            second,
            last,
            title
        },
        email,
        phone,
        location: {
            street : {
                number,
                name,
            },
            city,
            country,
            postcode
        }
    } = data;

    const address = `${title} ${last}, ${number} ${name}, ${city}, ${country}, ${postcode}`;

    return (
        `<section class="about-user">
            <div class="user-info-area">
                <p class="user-info-area__name">${first} ${second || ''} ${last}</p>
                <p class="user-info-area__email">email: <span>${email}</span></p>
                <p class="user-info-area__phone">phone: <span>${phone}</span></p>
                <p>${address}</p>
            </div>
            <ul class="activity-area">
                <li class="activity-area__item">
                    <a href="#">
                        <span>11</span>
                        new messages
                       </a>
                </li>
                <li class="activity-area__item">
                    <a href="#">
                        <span>0</span>    
                        new requests
                    </a>
                </li>
                <li class="activity-area__item">
                    <a href="#">
                        <span>7</span>
                        new photos        
                    </a>
                </li>
            </ul>
        </section>`
    );
};

export default class AboutUser extends AbstractComponent {
    constructor(data) {
        super();
        this._data = data;
    }

    getTemplate() {
        return createTemplate(this._data);
    }
}
