import Api from './api';
import Sidebar from './components/sidebar/sidebar';
import AboutUser from "./components/about-user/about-user";
import Loading from "./components/loading/loading";
import FeaturesController from './controllers/features-controller';

import {RenderPosition} from './utils/constants';
import {render} from './utils/utils';

import './main.css';

const bodyNode = document.querySelector(`body`);
const mainNode = bodyNode.querySelector(`.main`);
const infoNode = mainNode.querySelector(`.info-area`);

const loading = new Loading();
const loadingNode = loading.getElement();

render(infoNode, loadingNode, RenderPosition.BEFORE_END);

const api = new Api();

const urls = [
    api.getFriendsList(),
    api.getPosts(),
];

api.getUserData()
  .then((data) => {
      const requestResult = data.results[0];

      const sidebar = new Sidebar(requestResult.picture);
      const sidebarNode = sidebar.getElement();

      const aboutUser = new AboutUser(requestResult);
      const aboutUserNode = aboutUser.getElement();

      render(infoNode, aboutUserNode, RenderPosition.AFTER_BEGIN);
      render(mainNode, sidebarNode, RenderPosition.AFTER_BEGIN);
  }).finally(() => {
    loadingNode.remove();
    loading.removeElement();

    Promise.allSettled(urls)
        .then(responses => {
            const featureController = new FeaturesController(infoNode, responses);
            featureController.render();
        });
    });


