import AbstractSmartComponent from "../abstract-smart-component";
import './friends.css';

const createFriendItem = (data) => {
    const {avatar, first_name, last_name, email} = data;
    return (
        `<li class="friends-list__item">
            <img src='${avatar}' alt='${name} ${last_name}' />
            <p>${first_name}</p>
            <p>${last_name}</p>
            <p>email: <span>${email}</span></p>
        </li>`
    )
};

const createTemplate = (data) => {
    const friendsList = data
        .map((dataItem) => createFriendItem(dataItem))
        .join('');

    return (
        `<section class="friends-list-wrapper">
            <ul class="friends-list">
                ${friendsList}
            </ul>
        </section>`
    );
};

export default class Friends extends AbstractSmartComponent {
    constructor(friendsList) {
        super();
        this._friendsList = friendsList;
    }

    getTemplate() {
        return createTemplate(this._friendsList);
    }

    rerender(data) {
        this._friendsList = data;
        super.rerender();
    }

    recoveryListeners() {}
}
