import FriendsModel from "../models/friends-model";
import FriendsController from './friends-controller';
import Posts from "../components/posts/posts";
import FeaturesArea from "../components/features-area/features-area";
import Search from "../components/search/search";

import {removeClass, trottling} from '../utils/utils';
import {render, remove} from "../utils/utils";
import {RenderPosition, TabType} from "../utils/constants";

export default class FeaturesController {
    constructor(container, data) {
        const [friendsData, postsList] = data;
        const friendsList = friendsData.value.data;

        this._search = new Search();

        this._friendsModel = new FriendsModel();
        this._friendsModel.setFriends(friendsList);

        this._featuresArea = new FeaturesArea();

        this._posts = new Posts(postsList);

        this._featureRenderPlace = null;
        this._dataType = TabType.FRIENDS;

        this._friendsController = new FriendsController(this._friendsModel);

        this._container = container;
    }

    render() {
        render(this._container, this._featuresArea.getElement(), RenderPosition.BEFORE_END);

        this._featureRenderPlace = this._container.querySelector(`.feature-wrapper`);
        render(this._featureRenderPlace, this._search.getElement(), RenderPosition.BEFORE_END);
        this._friendsController.render(this._featureRenderPlace);

        this.setTabsListener();
        this.setFilterListener();
    }

    setTabsListener() {
        this._featuresArea.setTabsButtonClickHandler((evt) => {
            evt.preventDefault();

            const dataType = evt.target.dataset.type;

            if (this._dataType === dataType) {
                return false;
            }

            const className = 'features-tabs__item--active';
            removeClass(this._container, className);
            evt.target.classList.add(className);

            if (dataType && dataType === 'friends') {
                render(this._featureRenderPlace, this._search.getElement(), RenderPosition.BEFORE_END);
                this._friendsController.render(this._featureRenderPlace);

                remove(this._posts);
                this._posts.removeElement();

                this._dataType = dataType;

                return false;
            } else if (dataType && dataType === 'posts') {
                render(this._featureRenderPlace, this._posts.getElement(), RenderPosition.BEFORE_END);
                remove(this._search);
                this._friendsController.remove();

                this._dataType = dataType;

                return false;
            }

            return true;
        });
    }

    setFilterListener() {
        const trottledSearch = trottling((evt) => {
            evt.preventDefault();
            const query = evt.target.value;
            const filteredFriends = this._friendsModel.filterFriendsByQuery(query);
            this._friendsController.getFilteredFriends(filteredFriends);
        }, 3000);

        this._search.setSearchStringChangeHandler(evt => trottledSearch(evt));

        this._search.setFormSubmitHandler((evt) => {
            evt.preventDefault();
            const query = evt.target.value;

            const filteredFriends = this._friendsModel.filterFriendsByQuery(query);

            this._friendsController.getFilteredFriends(filteredFriends);
        })
    }
}
