import AbstractComponent from "../abstract-component";
import './pagination.css';

const createTemplate = (pageCount) => {
    const controlsArray = [];

    for (let i = 1; i <= pageCount; i++) {
        controlsArray.push(`
            <button
                type="button"
                data-count="${i}"
                class="pagination__button ${i === 1 ? 'pagination__button--active' : ''}"
            >
                ${i}
            </button>
        `);
    }

    return (
        `<div class="pagination">
           ${controlsArray.join('')}
        </div>`
    );
};

export default class Pagination extends AbstractComponent {
    constructor(pageCount) {
        super();
        this._pageCount = pageCount;
    }

    getTemplate() {
        return createTemplate(this._pageCount);
    }

    setPaginationButtonClickHandler(handler) {
        this.getElement().addEventListener(`click`, handler);
    }
}
