import AbstractComponent from "../abstract-component";
import {TabType} from '../../utils/constants';

import './feature-area.css';

const createTemplate = () => {
    return (
        `<section class="feature-wrapper">
            <div class="features-tabs">
                <button class="features-tabs__item features-tabs__item--active" data-type="${TabType.FRIENDS}" type="button">Friends</button>
                <button class="features-tabs__item" data-type="${TabType.POSTS}" type="button">Posts</button>
            </div>

        </section>`
    );
};

export default class FeaturesArea extends AbstractComponent {
    getTemplate() {
        return createTemplate();
    }

    setTabsButtonClickHandler(handler) {
        this.getElement()
            .querySelector(`.features-tabs`)
            .addEventListener(`click`, handler);
    }
}
