import Friends from "../components/friends/friends";
import Pagination from "../components/pagination/pagination";

import {render, remove} from "../utils/utils";
import {RenderPosition} from "../utils/constants";

import {removeClass, getPageCount, sliceList} from '../utils/utils';
import {FRIENDS_COUNT} from '../utils/constants';

export default class FriendsController {
    constructor(friendsList) {
        this._friendsList = friendsList.getFriends();
        this._pagesCount = getPageCount(this._friendsList);

        this._currentSliceCountPosition = 0;
        const stopSlicePoint = this._getStopSlicePoint();

        const preparedData = sliceList(this._friendsList, this._currentSliceCountPosition, stopSlicePoint);

        this._friends = new Friends(preparedData);

        if (this._pagesCount > 1) {
            this._pagination = new Pagination(this._pagesCount);
        }
    }

    _getStopSlicePoint(point = 0) {
        return point + FRIENDS_COUNT;
    }

    render(container) {
        render(container, this._friends.getElement(), RenderPosition.BEFORE_END);

        if (this._pagination) {
            render(container, this._pagination.getElement(), RenderPosition.BEFORE_END);
            this.setPaginationListener();
        }
    }

    remove() {
        remove(this._friends);
        this._friends.removeElement();

        remove(this._pagination);
        this._pagination.removeElement();
    }

    getFilteredFriends(friendsList) {
        this._currentSliceCountPosition = 0;
        const stopSlicePoint = this._getStopSlicePoint();
        const preparedData = sliceList(friendsList, this._currentSliceCountPosition, stopSlicePoint);
        this._pagesCount = getPageCount(friendsList);

        this._friends.rerender(preparedData);

        if (this._pagesCount > 1) {
            this._pagination.show();

            return false
        }

        this._pagination.hide();
    }

    setPaginationListener() {
        this._pagination.setPaginationButtonClickHandler((evt) => {
            evt.preventDefault();

            const paginationContainer = this._pagination.getElement();
            const className = 'pagination__button--active';
            removeClass(paginationContainer, className);
            evt.target.classList.add(className);

            const clickedPaginationButton = parseInt(evt.target.dataset.count);
            const gap = clickedPaginationButton - 1;
            this._currentSliceCountPosition = clickedPaginationButton === 1 ?
                0 :
                this._currentSliceCountPosition + (FRIENDS_COUNT * gap);

            const stopSlicePoint = this._getStopSlicePoint(this._currentSliceCountPosition);
            const preparedData = sliceList(this._friendsList, this._currentSliceCountPosition, stopSlicePoint);
            this._friends.rerender(preparedData)
        });
    }
}
