import AbstractComponent from "../abstract-component";
import './sidebar.css';

const createTemplate = (data) => {
    return (
        `<section class="sidebar">
            <img src="${data.medium}" alt="" class="sidebar__photo">
            <form class="user-photo-form">
                <label for="user-photo" class="user-photo-form__label">
                    Change photo
                </label>
                <input type="file" id="user-photo" class="visually-hidden" accept="image/*" />
            </form>
            <ul class="sidebar-menu">
                <li class="sidebar-menu__item"><a href="#">My account</a></li>
                <li class="sidebar-menu__item"><a href="#">Messages</a></li>
                <li class="sidebar-menu__item"><a href="#">News</a></li>
                <li class="sidebar-menu__item"><a href="#">Photos</a></li>
                <li class="sidebar-menu__item"><a href="#">Video</a></li>
                <li class="sidebar-menu__item"><a href="#">Bookmarks</a></li>
            </ul>
        </section>`
    );
};

export default class Sidebar extends AbstractComponent {
    constructor(data) {
        super();
        this._data = data;

        this._subscribeOnEvents();
    }

    getTemplate() {
        return createTemplate(this._data);
    }

    _subscribeOnEvents() {
        const photo = this.getElement().querySelector(`#user-photo`);
        const image = this.getElement().querySelector(`.sidebar__photo`);

        photo.addEventListener(`change`, (evt) => {
            const target = evt.target;
            const files = target.files;

            if (FileReader && files && files.length) {
                const fr = new FileReader();
                fr.onload = function () {
                    image.src = fr.result;
                };
                fr.readAsDataURL(files[0]);
            }
        });
    }
}
